













































































































import Vue from "vue";
import About from "./components/About.vue";
import ContactUs from "./components/ContactUs.vue";
import Gallery from "./components/Gallery.vue";
import Home from "./components/Home.vue";
import Footer from "./components/Footer.vue";

export default Vue.extend({
  name: "App",

  components: { Home, About, Gallery, ContactUs, Footer },

  data: () => ({
    x: 0 as any,
    offsetTop: 0 as number,
    fab: null as any,
    drawer: false as boolean,
    isSmScreen: false as boolean,
    menu: [
      { text: "Home", to: "#home", icon: "mdi-home-outline" },
      { text: "About", to: "#aboutus", icon: "mdi-information-outline" },
      { text: "Gallery", to: "#gallery", icon: "mdi-image-multiple-outline" },
      { text: "Contact", to: "#contact", icon: "mdi-book-open-blank-variant" },
    ],
  }),

  methods: {
    onScroll(e: any) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 60;
    },
    toTop() {
      this.$vuetify.goTo("#home");
    },
    resizeScreen() {
      this.isSmScreen = window.innerWidth < 900;
    },
  },

  watch: {
    isSmScreen(value) {
      if (!value) {
        if (this.drawer) this.drawer = false;
      }
    },
  },

  mounted() {
    window.addEventListener("scroll", () => {
      this.offsetTop = window.pageYOffset;
    });
    this.resizeScreen();
    window.addEventListener("resize", this.resizeScreen, { passive: true });
  },
});
