





























































import Vue from "vue";
import UITitle from "./UI/UITitle.vue";
export default Vue.extend({
  components: { "ui-title": UITitle },
  props: ["isSmScreen"],
  data: () => ({
    rules: [
      (v: any) =>
        (typeof v != "undefined" && v.length > 0) || "Field is required",
    ],
    emailRules: [
      (v: any) => !!v || "E-mail is required",
      (v: any) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
  }),
});
